<template>
	<view class="imagesContentBor">
		<view class="imagesContentd">
			<!-- <messageUpload :isActive='false'></messageUpload> -->
		 
			<messageindex   v-for="(item,index) in items" :key="index" :item='item' :isActive='true'></messageindex>
		</view>

	</view>
</template>


<script>
	import {
		getCustomerNotice
	} from "@/api/index.js";
	import messageUpload from '@/components/message/messageUpload.vue'
	import messageindex from '@/components/message/messageindex.vue'
 
	// import md5 from 'crypto-js/md5'
	export default {
		components: {
			messageUpload,
			messageindex
		},
		data() {
			return {

           items:[]
			};
		},
 
		methods: {
			getCustomerNotice(optionsData) {
				let _this = this
				let params = {
					"processNodeTblId": optionsData.processNodeTblId,
					"tblId": optionsData.tblId,
					"sort": 2
				}
				getCustomerNotice(params).then(response => {
                   _this.items =response.data
					console.log('==response=====>', _this.items );
				});
			}
		},
		onLoad(options) {
			let optionsData = JSON.parse(options.item) 
			this.getCustomerNotice(optionsData)
		}
	};
</script>

<style scoped>
	.imagesContentBor {
		background: linear-gradient(0deg, #FFFFFF 0%, #FFFFFF 100%);
		min-height: 100vh;
	}

	.imagesContentd {
		padding: 0 20upx 40upx 20upx;
		padding-left: 30upx;

	}
</style>
